  .slider-wrapper {
    position: relative;
    height: 200px;
    overflow: hidden;
  }
  
  .slide {
    height: 70vh;
    background-size: cover !important;
  }
  
  .head3 {
    left: 0;
    transform: rotate(180deg) translateY(calc(50% + 0px));
  }  
  
  .slider-content .inner h2 {
    /* font-weight: 900; */
    margin: 0 auto;
    /* max-width: 840px; */
    /* color: white; */
    color: #ccdaee;

    font-size: 44px;
    line-height: 1.5;
  }
  
  @media (max-height: 600px) {
    .slider-content .inner h2 {
      font-size: 1.8rem;
    }    
  }
  
  @media (max-width: 640px) {
    .slider-content .inner h2 {
      font-size: 1.8rem;
    }
  }

.slide .head1 {
	transition: all 0.3s ease;
	transform: translateY(-20px);
	opacity: 0;
}

.slide .head3 {
	transition: all 0.3s ease;
	transform: translateY(20px);
	opacity: 0;
}

.slide .head2 {
	transition: all 0.3s ease;
	transform: translateY(20px);
	opacity: 0;
}

.slide section * {
	transition: all 0.3s ease;
}
.slide.animateIn.previous .head1,
.slide.current .head1,
.slide.animateIn.next .head1,
.slide.animateIn.previous .head3,
.slide.current .head3,
.slide.animateIn.next .head3,
.slide.animateIn.previous .head2,
.slide.current .head2,
.slide.animateIn.next .head2,
.slide.animateIn.previous section *,
.slide.current section *,
.slide.animateIn.next section * {
	transform: translateX(0);
	transition-delay: .9s;
					opacity: 1;
					transition-timing-function: ease-in-out;
}

.slide.animateIn.previous .head2,
.slide.animateIn.next .head2 {
	transition-delay: 1.5s;
					transition-timing-function: ease-in-out;
}

.slide.animateIn.previous .head3,
.slide.animateIn.next .head3 {
	transition-delay: 1.3s;
					transition-timing-function: ease-in-out;
				}
.slide.animateOut .head1 {
	transition-delay: .3s;
}

.slide.animateOut .head2 {
	transition-delay: .2s;
}
