div[class*=container] {
    text-align: center;
    width: 100%;
    height: 33%;
    display: flex;
    justify-content: center;
    align-items: center;
  }


  .container-1 { background: #333; }

  .container-3 { background: #8499B1; }

  .btn {
    position: relative;
    /* color: white; */
    color: #c6d3e7;

    width: 160px;
    height: 45px;
    line-height: 40px;
    transition: all 0.3s;
    margin: 0 auto;
}
      
      /* span {
      transition: all 0.3s;
      tranform: scale(1, 1);
    } */
  
  .btn::before, .btn::after {
    content: '';
    position: absolute;
    transition: all 0.3s;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
  }
  
  .btn-one::before {
      left: 4px;
      z-index: 1;
    
    opacity: 0;
    background: rgba(255, 255, 255, 0.1);
    transform: scale(0.1, 1);
  }
  
  .btn-one:hover::before {
    opacity: 1;
    transform: scale(1, 1);
  }
  
  .btn-one::after {
    transition: all 0.3s;
    border: 1px solid rgba(255, 255, 255, 0.5);
  }
  
  .btn-one:hover::after {
    transform: scale(1, .1);
    opacity: 0;
  }
/* BTN THREE */
.btn-three::before {
    opacity: 0;
    background: rgba(255, 255, 255, 0.1);
    transform: scale(1, 0.1);
  }
  
  .btn-three:hover::before {
    opacity: 1;
    transform: scale(1, 1);
  }
  
  .btn-three::after {
    transition: all 0.3s;
    border: 1px solid rgba(255, 255, 255, 0.5);
  }
  
  .btn-three:hover::after {
    transform: scale(1, .1);
    opacity: 0;
  }

